.App{
  background-color: #2E4158;
  height: 100%;
  width:100%;
  position: fixed;
  overflow: auto;
}

.home{
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  z-index: 1;
  background-color: transparent;
  opacity:0.6;

}

.home i{
  color:white;
  font-size:2.5em;
  -webkit-animation: fade-slide-up 1s;
          animation: fade-slide-up 1s;
}

.home i:hover {
  color: #ab9fd4;
}

@-webkit-keyframes fade-slide-up{
  0%{
    opacity:0;
    -webkit-transform: translateY(2rem);
            transform: translateY(2rem);
  }

  100%{
    opacity:1;
    -webkit-transform: none;
            transform: none;
  }
}

@keyframes fade-slide-up{
  0%{
    opacity:0;
    -webkit-transform: translateY(2rem);
            transform: translateY(2rem);
  }

  100%{
    opacity:1;
    -webkit-transform: none;
            transform: none;
  }
}



/*Landing page*/

.banner{
    background-color: transparent;
    opacity:.8;
    width:75%;
    margin:auto;
    text-align: center;
    padding: 15em 0 15em 0 ;
  }
  
  .banner h1{
    -webkit-animation: fade-slide-up .5s ;
            animation: fade-slide-up .5s ;
    font-size: 66px;
    font-weight: bold;
    color:white;
  }

  .banner hr{
    -webkit-animation:line .5s;
            animation:line .5s;
    border-top: 3px solid #ab9fd4;
    width: 100%;
  
  }
  
  .banner .bottom{
    -webkit-animation:fade-slide-down .5s;
            animation:fade-slide-down .5s;
  }

  .banner .tagline{
    color: white;
    font-size: 20px;
  }
  
  .social_links{
    display: inline-flex;
    justify-content: space-between;
    width:20em;
  }
  
  .social_links i{
    color:white;
    font-size:4em;
    padding: 8px;
  }

  .social_links i:hover {
    -webkit-animation: bounce 1s;
            animation: bounce 1s;
    color: #ab9fd4;
}

  /*h1 effects*/
  @-webkit-keyframes fade-slide-up{
    0%{
      opacity:0;
      -webkit-transform: translateY(2rem);
              transform: translateY(2rem);
    }

    100%{
      opacity:1;
      -webkit-transform: none;
              transform: none;
    }
  }
  @keyframes fade-slide-up{
    0%{
      opacity:0;
      -webkit-transform: translateY(2rem);
              transform: translateY(2rem);
    }

    100%{
      opacity:1;
      -webkit-transform: none;
              transform: none;
    }
  }

  @-webkit-keyframes line{
    0%{
      opacity:0;
      -webkit-transform: scaleX(0);
              transform: scaleX(0)
    }

    100%{
      opacity:1;
    }
  }

  @keyframes line{
    0%{
      opacity:0;
      -webkit-transform: scaleX(0);
              transform: scaleX(0)
    }

    100%{
      opacity:1;
    }
  }

  @-webkit-keyframes fade-slide-down{
    0%{
      opacity:0;
      -webkit-transform: translateY(-2rem);
              transform: translateY(-2rem);
    }

    100%{
      opacity:1;
      -webkit-transform: none;
              transform: none;
    }
  }

  @keyframes fade-slide-down{
    0%{
      opacity:0;
      -webkit-transform: translateY(-2rem);
              transform: translateY(-2rem);
    }

    100%{
      opacity:1;
      -webkit-transform: none;
              transform: none;
    }
  }
  



  /*social links effects*/
  @-webkit-keyframes bounce {
      0%, 20%, 60%, 100% {
          -webkit-transform: translateY(0);
          transform: translateY(0);
      }
  
      40% {
          -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
      }
  
      80% {
          -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
      }
  }
  @keyframes bounce {
      0%, 20%, 60%, 100% {
          -webkit-transform: translateY(0);
          transform: translateY(0);
      }
  
      40% {
          -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
      }
  
      80% {
          -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
      }
  }

  @media screen and (max-width: 768px) {
    .banner{
      width:100%;
